import Vue from 'vue'
import Router from 'vue-router'
import ProductList from '@/components/ProductList'
import UpLoader from '@/components/UpLoader'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ProductList',
      component: ProductList
    }, {
      path: '/UpLoader',
      name: 'UpLoader',
      component: UpLoader
    }
  ]
})



