// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import axios from 'axios'
import qs from 'qs'
import { Uploader, Button, Sticky, List, Cell } from 'vant'

// 静态资源
import resList from '../static/config'

Vue.config.productionTip = false

Vue.prototype.resList = resList
Vue.prototype.$axios = axios
Vue.prototype.qs = qs

Vue.use(Uploader).use(Button).use(Sticky).use(List).use(Cell)
// Vue.use(Button)
// Vue.use(Toast)
// Vue.use(Sticky)
// Vue.use(List)
// Vue.use(Cell)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})

// af5aa311b1cd47f4a15df29bdc8e94dc
axios.interceptors.request.use(
  config => {
    // config.headers.token = store.state.token
    config.headers.token = 'af5aa311b1cd47f4a15df29bdc8e94dc'
    config.headers.source = 0 // PC端请求标志
    // eslint-disable-next-line eqeqeq
    if (config.method == 'post') {
      if (!(config.headers && config.headers['Content-Type'])) {
        config.transformRequest = [
          function (data) {
            return qs.stringify(data)
          }
        ]
      }
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

axios.interceptors.response.use(
  res => {
    // console.log(res)
    if (res && res.hasOwnProperty('data') && res.data.hasOwnProperty('code')) {
      res = res.data
    }
    if (
      res &&
      // eslint-disable-next-line eqeqeq
      res.code != 1 &&
      // eslint-disable-next-line eqeqeq
      (res.exceptionCode == 1000 || res.exceptionCode == 1018)
    ) {
      router.replace({
        path: '/login',
        toUrl: { redirect: router.currentRoute.fullPath }
      })
      return null // Promise.reject(res.data);
    }
    return res
  },
  err => {
    return Promise.reject(err)
  }
)



