<template>
  <div class="product-list-wrapper">
    <van-sticky>
      <div class="user-title">账户名字</div>
    </van-sticky>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="item in list" :key="item.productId" :title="item.productName" :label="item.oeNumber"
        @click="upLoader(item)" value="" />
    </van-list>
  </div>
</template>

<script>
// import { List, Cell, Sticky } from 'vant'

export default {
  name: 'ProductList',
  // components: { [List.name]: List, [Cell.name]: Cell, [Sticky.name]: Sticky },
  props: {},
  data () {
    return {
      list: [],
      loading: false,
      finished: false
    }
  },
  watch: {},
  computed: {},
  methods: {
    onLoad () {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.resList.data[i])
        }

        // 加载状态结束
        this.loading = false

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true
        }
      }, 1000)
    },
    upLoader (item) {
      this.$router.push({ path: '/UpLoader', query: item })
    }
  },
  created () {
  },
  mounted () { }
}
</script>
<style scoped lang='scss'>
.product-list-wrapper {
  .van-cell >>> .van-cell__title {
    text-align: left;
  }
  .user-title {
    height: 40px;
    width: 100%;
    background-color: #e6e6e6;
    color: black;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
  }
}
</style>



