<template>
  <div class="up-load-wrapper">
    <van-sticky>
      <div class="user-title">{{ productInfo.productName }}</div>
    </van-sticky>
    <div class="img-box">
      <van-uploader :after-read="afterRead" v-model="fileList" multiple :max-count="5" capture="camera" />
    </div>
    <van-button type="primary" @click="upLoad">保存上传</van-button>
  </div>
</template>

<script>
import { Toast } from 'vant'
// image-compressor 图片压缩
export default {
  name: 'UpLoader',
  components: { [Toast.name]: Toast },
  props: {},
  data () {
    return {
      productInfo: {},
      fileList: []
    }
  },
  watch: {},
  computed: {},
  methods: {
    afterRead (file) {
      let _this = this
      let url = '/app/app/v2/member/upload/uploadImage'
      if (Array.isArray(file)) { // 多文件
        file.forEach(v => {
          v.status = 'uploading'
          v.message = '上传中...'
          _this.$axios.post(url, { imageByBase64: v.content, type: 1 })
            .then(res => {
              if (res.code === 1) {
                v.url = res.data
                v.status = 'done'
                v.message = '上传成功'
              } else {
                v.status = 'failed'
                v.message = '上传失败'
              }
            })
        })
      } else {
        file.status = 'uploading'
        file.message = '上传中...'
        console.log(file)
        _this.$axios.post(url, { imageByBase64: file.content, type: 1 })
          .then(res => {
            if (res.code === 1) {
              file.url = res.data
              file.status = 'done'
              file.message = '上传成功'
            } else {
              file.status = 'failed'
              file.message = '上传失败'
            }
          })
      }
    },
    upLoad () {
      let urlArr = []
      if (this.fileList.length === 0) {
        // eslint-disable-next-line no-undef
        Toast('请上传图片后保存')
      } else {
        // let param = []
        // let url = ''
        this.fileList.forEach(v => {
          urlArr.push(v.url)
        })
      }
      console.log('urlArr: ', urlArr)
    }
  },
  created () {
    this.productInfo = this.$route.query
  },
  mounted () { }
}
</script>
<style scoped lang='scss'>
.up-load-wrapper {
  .user-title {
    height: 40px;
    width: 100%;
    background-color: #e6e6e6;
    color: black;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
  }
  .img-box {
    padding: 10px;
  }
}
</style>



